import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';

class App extends Component {

	constructor() {
		//you must call the Component constructor function using super(), before setting any properties in this class - this is a react standard
		super();

		//this is a react standard, you must call this.state
		this.state = {
			picArr : ["thor.jpg",
					  "ironman.jpg",
					  "cap.jpg",
					  "marvel.jpg",
					  "okoye.jpg",
					  "blackwidow.jpg",
					  "hawkeye.jpg",
					  "antman.jpg",
					  "nebula.jpg",
					  "rocket.jpg",
					  "hulk.jpg",
					  "warmachine.jpg"],
			topScore: 0,
			selected: [],
			score: 0,
			label: "Click an image to begin!",
			toShake: "",
			info : "dispHide"
		}
	}

	clickMe = (event) => {

		let selected = [...this.state.selected] //for images that are clicked

		let images = [...this.state.picArr];

		let score = this.state.score

		let topScore = this.state.topScore

		let label = this.state.label

		let toShake = this.state.toShake

		let title = event.target.textContent

		let info = this.state.info

		//CHECKING
		if (title !== "Clicky-Game") {
			if (selected.includes(event.target.src)) {

				label = "You've guessed incorrectly!"
				score = 0
				selected = []
				// event.target.className = "animated shake faster"
				// shaked = true
				toShake = "animated shake faster"
				if (this.state.info === "")
					info = "dispHide"

			} else {

				toShake = ""			
				selected.push(event.target.src)
				score++

				if (score === 12) {

					topScore++
					label = "You Won!"
					score = 0
					selected = []

				} else {

					label = "You guessed correctly!"

					if (score > topScore) {
						topScore++
					} else {
						topScore = this.state.topScore
					  }

				  }

			  }
		} else {

				label = "Click an image to begin!"
				score = 0
				selected = []
				topScore = 0

				if (this.state.info === "")
					info = "dispHide"
				else 
					info = ""

		  }

		// if (!shaked) {
		// 	this.shuffle(this.state.picArr)
		// 	this.setState( {
		// 		images,  
		// 		topScore, 
		// 		selected,
		// 		score,
		// 		label
		// 	})
		// } else {
		// 	shaked = false
		// 	setTimeout(
		// 		function() {
		// 			this.shuffle(this.state.picArr)
		// 			this.setState( {
		// 				images,  
		// 				topScore, 
		// 				selected,
		// 				score,
		// 				label
		// 			})
		// 		}
		// 		.bind(this),700
		// 	)
		// }

		this.shuffle(this.state.picArr)
		this.setState( {
			images,  
			topScore, 
			selected,
			score,
			label,
			toShake,
			info
		})

	}
 
	shuffle = (array) => {

		//https://javascript.info/task/shuffle

		for (let i = array.length - 1; i > 0; i--) {
		    let j = Math.floor(Math.random() * (i + 1));
		    [array[i], array[j]] = [array[j], array[i]];
		}

	}

	createTable = () => {

		//https://blog.cloudboost.io/for-loops-in-react-render-no-you-didnt-6c9f4aa73778

		let html = []

		for (let i = 1 ; i <= this.state.picArr.length; i++) {
			html.push(<img className={this.state.toShake} src={`./images/${this.state.picArr[i-1]}`} alt={`./images/${this.state.picArr[i-1]}`} onClick={this.clickMe} key={i}/>)
				
		}

		return html

	}

  //this function must be called render
  render() {

    return (

    	<div id="parentContainer">
	    	<nav className="navbar navbar-expand-lg navbar-dark sticky-top">
				<div className="container navContainer">
					<div className="collapse navbar-collapse justify-content-center" id="navbarNavDropdown">
						<ul className="navbar-nav row hide-on-med-and-down">
							<li className="nav-item col s4 gametitle" onClick={this.clickMe}>Clicky-Game</li>
							<li className="nav-item col s4 center-align">{this.state.label}</li>
							<li className="nav-item col s4 right-align">
								<span>Score: {this.state.score}</span> | <span>Top Score: {this.state.topScore}</span>
							</li>
						</ul>
						<ul className="navbar-nav row show-on-med-and-down hide-on-large-only hide-on-small-only">
							<li className="nav-item col s4 gametitle" onClick={this.clickMe}>Clicky-Game</li>
							<li className="nav-item col s4 center-align">{this.state.label}</li>
							<li className="nav-item col s4 right-align">
								<span>Score: {this.state.score}</span> | <span>Top Score: {this.state.topScore}</span>
							</li>
						</ul>
						<ul className="navbar-nav row hide-on-med-and-up show-on-small-only">
							<li className="nav-item col s4 gametitle" onClick={this.clickMe}>Clicky-Game</li>
							<li className="nav-item col s4 center-align">{this.state.label}</li>
							<li className="nav-item col s4 right-align">
								<span>Score: {this.state.score}</span> | <span>Top Score: {this.state.topScore}</span>
							</li>
						</ul>
					</div>
			 	</div>
			</nav>
			<header className={`${this.state.info} center-align header`} id="gameInfo">
		 		<h1>CLICKY GAME</h1>
    			<h2>Click on an image to earn points, but don't click on it any more than once!</h2>
		 	</header>
	    	<div id="mainContainer">
	    		{this.createTable()}
	    	</div>
	    	<footer className="footer page-footer">	
				<div className="footer-copyright">
				    <div className="container">
					    <div className="col s12 center-align">Â© 2019 Copyright</div>
				    </div>
			  </div>
			</footer>
	    </div>

    );

  }
}

export default App;
